'use strict';

var Component = require('../component');

var defaults = {
	prefix: 'ui',
	type: null, // push, file
	element: {
		tag: 'span'
	},
	ripple: {
		duration: '300',
		equation: 'ease-out'
	}
};

/**
 * Button control class
 * @class
 * @extends Control
 * @since 0.0.1
 * @example
 * var button = new Button({
 *   label: 'Primary raised button',
 *   type: 'raised',
 *   primary: true
 * }).on('press', function(e) {
 *   console.log('button press', e);
 * }).inject(document.body);
 */
class Button extends Component {

	/**
	 * The init method of the Button class
	 * @param  {Object} options [description]
	 * @private
	 * @return {Object} The class instance
	 */
	init(options) {
		super.init(options);

		this.options = [ defaults, options ].reduce(Object.assign, {});

		return this;
	}

	/**
	 * Build button's method
	 * @override
	 * @return {void}
	 */
	build(props) {
		super.build(props);

		var opts = this.options;
		var type = opts.type;

		opts.label = opts.label || opts.n;

		if (type === null) {
			type = 'icon-text';
		}

		if (opts.name) {
			this.attribute('data-name', opts.name);
		}

		this.attribute('title', opts.text);

		if (opts.icon) {
			this._initIcon(type, opts.icon || opts.name);
		}

		this._initLabel(type);

		if (opts.ripple) {
			this._initSensor();
		} else {
			this.sensor = this.element;
		}
	}

	/**
	 * [_onElementMouseDown description]
	 * @param  {event} e
	 * @return {void}
	 */
	press(e) {
		e.preventDefault();

		if (this.state === 'disabled') return;

		this.emit('press', e);

		return this;
	}

	/**
	 * [_initIcon description]
	 * @param  {string} type
	 * @return {string}
	 */
	_initIcon(type, name) {

		var code = name;

		var prop = {
			'tag': 'span.ui-icon'
		};

		this.icon = new Component(prop).inject(this.element);

		// prepare use of svg
		// this.iconsvg = new Element('svg', prop).inject(this.element);
		// this.svguse = new Element('use').inject(this.iconsvg);

		// this.iconsvg.attribute('viewBox', '0 0 24 24');
		// this.svguse.attribute('xlripple:href','/vendor/icon/content-send.svg');

		if (code) this.icon.addClass(code);
	}

	/**
	 * [_initLabel description]
	 * @param  {string} type
	 * @return {void}
	 */
	_initLabel(type) {
		var options = this.options;

		var position = 'bottom';
		if (type === 'text-icon') {
			position = 'top';
		}

		if (this.options.label !== null) {
			var text = options.label || options.text;
			this.label = new Component({tag: 'span.ui-text'}).inject(this.element, position);
			this.label.text(text);
		}
	}

	/**
	 * [_initText description]
	 * @return {void}
	 */
	_initSensor() {

		this.sensor = new Component({
			tag: 'div.ui-sensor'
		}).inject(this.element);

		this.sensor.addEvent('click', (e) => {
			this.press(e);
		}).addEvent('mousedown', (e) => {
			this.addClass('is-active');		
			this._showRipple(e);
		}).addEvent('mouseup', () => {
			this.removeClass('is-active');	
			if (!this.rippleActive)
				this._hideRipple();
		}).addEvent('mouseleave', (e) => {
			this.removeClass('is-active');		
			this._hideRipple(e);
		});
	}

	/**
	 * _showRipple methosd
	 * @param  {string} ripple
	 * @param  {string} x
	 * @param  {string} y
	 * @param  {Object} coord
	 * @return {void}
	 */
	_showRipple(e) {
		
		if (!this.size) {
			this.size = this.offset();
		}

		if (!this.ripple)
		this.ripple = new Component({
			tag:'span.ui-ripple'
		}).inject(this, 'top');

		if (this.animation) {
			this.animation.stop();
		}

		var coord = this._rippleCoord(this.size);
		var options = this.options.ripple;

		var startLeft = (e.offsetX || this.size.width / 2) - 5;
		var startTop = (e.offsetY || this.size.height / 2) - 5;

		this.ripple.style({
			left: startLeft + 'px',
			top: startTop + 'px',
			width: '5px',
			height: '5px',
			opacity: 1
		});

		this.rippleActive = true;

		this.animation = this.ripple.animate({
			width: coord.size * 2,
			height: coord.size * 2,
			left: coord.left,
			top: coord.top,
			opacity: 0.2,
			duration: options.duration,
			easing: options.equation,
			complete: () => {
				this.rippleActive = false;
				if (!this.hasClass('is-active'))
					this._hideRipple();
			}
		});
	}

	_hideRipple() {
		var options = this.options.ripple;

		if (!this.ripple) return;

		this.animation.stop();

		this.animation = this.ripple.animate({
			opacity: 0,
			duration: '300',
			easing: options.equation
		});
	}

	/**
	 * [_rippleCoord description]
	 * @return {Object} Size and top
	 */
	_rippleCoord(offset) {
		var size = offset.width;
		var top = -offset.height / 2;

		if (offset.width > offset.height) {
			size = offset.width;
			top = -(offset.width - offset.height / 2);
		} else if (offset.width < offset.height) {
			size = offset.height;
			top = (offset.width - offset.height) / 2;
		}

		return {
			size: size,
			top: top,
			left: size / -2
		};
	}
}

module.exports = Button;
